.header{
    margin-top: 0;
    margin-bottom: 120px;
    width: 100%;
    height: 962px;
    background-image: url("../img/imgonline-com-ua-Resize-DlRXsc4WVsD3h.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    

    &__container{
        margin: 0 165px;

    }
    &__wrapper{
        width: 100%;
    }
    &__nav{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__menu{
        max-width: 1061px;
        width: 100%;
        margin-top: 85px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    &__logo{
        width: 185px;
        margin-top: 85px;
        font-size: 24px;
        font-weight: 700;
        font-family: $font-family;

        a{
            text-decoration: none;
            color: white;
        }
    }
    &-links__wrapper{
        display: flex;
    }
    &__link{
        margin-left: 50px;
        font-size: 18px;
        font-family: $font-family;
        &:first-child{
            margin-left: 0;
        }
        a{
            text-decoration: none;
            color: white;
        }
    }
    &__demo-btn{
        font-size: 18px;
        font-family: $font-family;
        //margin-left: 176px;
        padding: 12px 32px;
        background-color: #B85C38;
        border-radius: 40px;
        @include transition();
        &:hover{
            transform: scale(0.9);
            background-color: #FF804E;
        }
        a{
            text-decoration: none;
            color: white;
        }
    }
    

    @media screen and(max-width:1199px) {
       &__container{
            margin: 0 100px;
        }
    }

    @media screen and(max-width:1023px) {
        &__logo{
            font-size: 22px;
        }
        &__link{
            font-size: 15px;
            margin-left: 35px;
        }
        &__demo-btn{
            padding: 8px 25px;
            font-size: 15px;
        } 
     }
    
    @media screen and(max-width:884px) {
        &__container{
            margin: 0 40px;
        }
        &__logo{
            width: 150px;
        }
    }

    @media screen and(max-width:767px) {
        margin-bottom: 70px;
        &__menu{
            display: block;
            right: 200%;
            margin-top: 0;
            top: 0;
            
            transition: all 0.3s ease;
            position: absolute;
            width: 100%;
            height: 100vh;
            background-color: #B85C38;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            z-index: 50;
            &_active{
                right: 0;
                display: flex;
                justify-content: center;     
            }
        }
        &__logo{
            position: absolute;
            top: 0px;
            left: 65px;
              
            &_active{
                display: none;
            } 
        }
        
        &-links__wrapper{
            flex-direction: column;
            align-items: center;
            justify-content: left;
        }
        &__link{
            display: none;
            &_active{
                display: block;
                margin-left: 0;
                margin-top: 20px;
                font-size: 30px;
            }
            
        }
        &__demo-btn{
            display: none;
            &_active{
                display: block;
                background-color: white;
                font-size: 30px;
                margin-top: 20px;
                z-index: 50;
                
                a{
                    color: #B85C38;
                    &:hover{
                        color: white;
                    }
                }
            }
            
        } 



        .burger-btn{
                z-index: 100;
                display: flex;
                position: absolute;
                width: 30px;
                height: 20px;
                top: 85px;
                right: 65px;
                &::before,&::after{
                    content: '';
                    background-color: white;
                    position: absolute;
                    width: 100%;
                    height: 2px;
                }
                &::before{
                    top: 0;
                }

                &::after{
                    bottom: 0;
                    
                }
                span{
                    position: absolute;
                    background-color: white;
                    left: 0;
                    width: 100%;
                    height: 2px;
                    top: 9px;
                    transition: transform 0.3s ease;
                }

                &_active{
                    
                    position: fixed;
                    span {
                        transform: rotate(45deg); 
                    }
                  
                    &::before {
                        transform: translateY(9px) rotate(-45deg); 
                    }
                    &::after {
                        transform: translateY(-9px) rotate(-45deg); 
                    }
                    
            }

        }
    }
}

//BEST PLACE BLOCK

.best-place{
    margin-top: 153px;
    &__container{
        margin: 0 165px;
    }
    &__wrapper{
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    &__circle{
        width: 80px;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background-color: #B85C38;
        border: 2px solid white;
        @include flex();
        cursor: pointer;
        overflow: hidden;
        position: relative;
        background: none;
        @include transition();
        &::before {
            transition: all 0.5s ease;
            content: "";
            width: 0%;
            height: 100%;
            background: #B85C38;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
        }                                
        &:hover::before {
            width: 100%;  
        }
        &:hover{
            transform: scale(0.9);
            background-color: #FF804E;
        }                               
    }
    &__img{
        fill: white;
    }
    &__title{
        margin-top: 40px;
        font-size: 72px;
        color: white;
        font-weight: 700;
        font-family: $font-family;
        text-align: center;
        text-shadow: 4px 2px 4px 0px rgba(0, 0, 0, 0.25);
    }
    &__description{
        text-align: center;
        margin-top: 16px;
        color: white;
        font-size: 24px;
        font-family: $font-family;
    }
    &__buttons{
        display: flex;
        margin-top: 40px;
    }
    &__demo{
        padding: 18px 32px;
        color: white;
        border-radius: 40px;
        background-color: #B85C38;
        font-size: 20px;
        font-family: $font-family;
        @include transition();
        &:hover{
            transform: scale(0.9);
            background-color: #FF804E;
        }
    }
    &__buy{
        margin-left: 24px;
        color: white;
        padding: 18px 32px;
        border-radius: 40px;
        border: 2px solid #FFF;
        font-size: 20px;
        font-family: $font-family;
        background-color: #00000000;

        margin-left: 24px;
        background: none;
        position: relative;
        overflow: hidden;
        @include transition();
        &::before {
            transition: all 0.5s ease;
            content: "";
            width: 0%;
            height: 100%;
            background: #B85C38;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
        }
        &:hover::before {
            width: 100%;
        }
        &:hover{
            transform: scale(0.9);
            background-color: #FF804E;
        }
    }
    &__buy-text{
        color: white;
    }


    @media screen and(max-width:1199px) {
        &__container{
            margin: 0 100px;
        }
        &__title{
            font-size: 64px;
        }
     }
 
     @media screen and(max-width:1023px) {
        &__title{
            font-size: 50px;
        }
        &__description{
            font-size: 20px;
        }
        &__demo{
            font-size: 18px;
            padding: 15px 28px;
        }
        &__buy{
            font-size: 18px;
            padding: 15px 28px;
        }
     }
     
     @media screen and(max-width:884px) {
        &__container{
            margin: 0 40px;
        }
     }
     
 
     @media screen and(max-width:767px) {
        margin-top: 153px + 85px + 28px;
        &__demo{
            font-size: 15px;
            padding: 12px 24px;
        }
        &__buy{
            font-size: 15px;
            padding: 12px 24px;
        }
     }

}









