                font-family: $font-family;
                font-weight: 700;
            }
            &__description{
                line-height:25.6px;
                font-size: 16px;
                color: #BDBDBD;
                font-family: $font-family;
                margin: 12px 0 12px 0;
                
            }
            span{
                margin-top: 12px;

            }
            span>a{
                font-size: 16px;
                color: #B85C38;
            }


            @media screen and(max-width:1381px) {
                width: 100%;
                max-width: 1110px;
                justify-content: flex-start;
            }

            @media screen and(max-width:1035px) {
                width: 100%;
                justify-content: center;
            }

            @media screen and(max-width:895px) {
                
                img{
                    width: 150px;
                }
            }
            
            @media screen and(max-width:800px) {
                margin: 0 0 20px 0;

                img{
                    margin-left: 0;
                    margin-right: 15px;
                }
                &__title{
                    font-size: 18px;
                }
                &__description{
                    font-size: 14px;
                } 
            }

            @media screen and(max-width:515px) {
                img{
                    width: 120px;
                }
                &__title{
                    font-size: 16px;
                }
                &__description{
