    }


    //DONT MISS BLOCK

    .dont-miss{
        display: flex;
        justify-content: center;
        background-color: #F7F9FC;
        padding: 100px 70px 100px 165px;
        &__wrapper{
            display: flex;
            gap: 30px;
            align-items: center;
        }
        &__title{
            line-height: 65.47px;
            font-size: 48px;
            font-weight: 700;
            font-family: $font-family;   
        }
        &__description{
            line-height: 32.74px;
            font-size: 24px;
            font-family: $font-family;
            color: #BDBDBD;
            margin: 24px 0 42px 0;
        }
        &__button{
            background-color: #B85C38;
            padding: 18px 32px;
            border-radius: 40px;
            @include transition();
            a{
                color: white;
                font-size: 20px;
                font-weight: 700;
            }
            &:hover{
                transform: scale(0.9);
                background-color: #FF804E;
              }
        }

        &__card{
            display: flex;
            flex-direction: column;
            transition: box-shadow 0.3s ease;
            &:hover{
                box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
            }
        }
        &__content{
            flex-direction: column;
            display: flex;
            span>a{
                color: #B85C38;
                font-size: 20px;
                text-decoration: none;
    
            }
            background-color: white;
            padding: 0 28px 79px 30px;
            border-bottom-left-radius: 8px;
            border-bottom-right-radius: 8px;    
        }
        &__content-title{
            line-height: 40.92px;
            font-size: 30px;
            font-weight: 700;
            font-family: $font-family;
            margin-top: 25px;
        }
        &__content-description{
            line-height: 27.28px;
            font-size: 20px;
            font-family: $font-family;
            margin: 24px 0 32px 0;
            color: #BDBDBD;
        }

        @media screen and(max-width:1280px) {
            &__wrapper{
                flex-wrap: wrap;

            }
        }
        @media screen and(max-width:1045px) {
            padding: 80px 60px 80px 100px;
            justify-content: center;
            &__wrapper{
                justify-content: flex-start;
            }
        }
        @media screen and(max-width:900px) {
            
            &__wrapper{
                justify-content: center;
            }
        }

        @media screen and(max-width:440px) {
            align-items: center;
            justify-content: center;
            padding: 0;
            &__content{
                width: 300px;
            }
            &__wrapper{
                margin-bottom: 40px;
            }
            &__text{
                display: flex;
                flex-direction: column;
                align-self: center;
                padding: 70px ;
            }
            &__title{
                font-size: 40px;

            }
            img{
                width: 300px;
            }
        }
        
    }

    //CLIENTS BLOCK

    .clients{
        display: flex;
        justify-content: center;
        margin-bottom: 113px;
        &__wrapper{
            display: flex;
            justify-content: center;
            gap: 30px;
            flex-wrap: wrap;
            margin: 113px 165px 0 165px;
        }
        &__card{
            display: flex;
            flex-direction: column;
            //justify-content: center;
            align-items: center;
        }
        &__img{
            width: 90px;
        }
        &__comment{
            margin-top: 24px;
            font-size: 24px;
            font-family: $font-family;
            line-height: 32.74px;
            text-align: center;
        }
        &__name{
            margin-top: 24px;
            font-size: 20px;
            font-family: $font-family;
            line-height: 27.28px;
            font-weight: 700;
        }
        &__job{
            margin-top: 4px;
