//FOOTER BLOCK

.footer{
    padding: 0 165px;
    background: #2D2424;
    margin-bottom: 0;
    border-bottom: none;
    &__wrapper{
        display: flex;
        gap: 30px;
        justify-content: center;
        border-bottom: 1px solid #473F3F;
        padding-bottom: 80px;
    }
    &__contacts{
        font-family: $font-family;
        display: flex;
        flex-direction: column;
        li:nth-child(2){
            margin-top: 24px;
        }
        li:first-child{
            margin-top: 80px;
            font-size: 36px;
            font-weight: 700;
            line-height: 49.1px;

        }
        li{
            line-height: 21.82px;
            color: white;
            margin-top: 16px;
            font-size: 16px;
        }
    }
    &__title-additional{
        margin-top: 80px;
        font-size: 24px;
        font-weight: 700;
    }
    
    &__links{
        margin-left: 30px;
        color: white;
        li:nth-child(2){
            margin-top: 34px;
        }
        li:first-child{
            margin-top: 80px;
            font-size: 24px;
            font-weight: 700;
        }
        li{
