@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;700&family=Nunito:wght@400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.header {
  margin-top: 0;
  margin-bottom: 120px;
  width: 100%;
  height: 962px;
  background-image: url("../img/imgonline-com-ua-Resize-DlRXsc4WVsD3h.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.header__container {
  margin: 0 165px;
}
.header__wrapper {
  width: 100%;
}
.header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__menu {
  max-width: 1061px;
  width: 100%;
  margin-top: 85px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}
.header__logo {
  width: 185px;
  margin-top: 85px;
  font-size: 24px;
  font-weight: 700;
  font-family: "Nunito";
}
.header__logo a {
  text-decoration: none;
  color: white;
}
.header-links__wrapper {
  display: flex;
}
.header__link {
  margin-left: 50px;
  font-size: 18px;
  font-family: "Nunito";
}
.header__link:first-child {
  margin-left: 0;
}
.header__link a {
  text-decoration: none;
  color: white;
}
.header__demo-btn {
  font-size: 18px;
  font-family: "Nunito";
  padding: 12px 32px;
  background-color: #B85C38;
  border-radius: 40px;
  transition: all ease 0.5s;
}
.header__demo-btn:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.header__demo-btn a {
  text-decoration: none;
  color: white;
}
@media screen and (max-width: 1199px) {
  .header__container {
    margin: 0 100px;
  }
}
@media screen and (max-width: 1023px) {
  .header__logo {
    font-size: 22px;
  }
  .header__link {
    font-size: 15px;
    margin-left: 35px;
  }
  .header__demo-btn {
    padding: 8px 25px;
    font-size: 15px;
  }
}
@media screen and (max-width: 884px) {
  .header__container {
    margin: 0 40px;
  }
  .header__logo {
    width: 150px;
  }
}
@media screen and (max-width: 767px) {
  .header {
    margin-bottom: 70px;
  }
  .header__menu {
    display: block;
    right: 200%;
    margin-top: 0;
    top: 0;
    transition: all 0.3s ease;
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: #B85C38;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 50;
  }
  .header__menu_active {
    right: 0;
    display: flex;
    justify-content: center;
  }
  .header__logo {
    position: absolute;
    top: 0px;
    left: 65px;
  }
  .header__logo_active {
    display: none;
  }
  .header-links__wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: left;
  }
  .header__link {
    display: none;
  }
  .header__link_active {
    display: block;
    margin-left: 0;
    margin-top: 20px;
    font-size: 30px;
  }
  .header__demo-btn {
    display: none;
  }
  .header__demo-btn_active {
    display: block;
    background-color: white;
    font-size: 30px;
    margin-top: 20px;
    z-index: 50;
  }
  .header__demo-btn_active a {
    color: #B85C38;
  }
  .header__demo-btn_active a:hover {
    color: white;
  }
  .header .burger-btn {
    z-index: 100;
    display: flex;
    position: absolute;
    width: 30px;
    height: 20px;
    top: 85px;
    right: 65px;
  }
  .header .burger-btn::before, .header .burger-btn::after {
    content: "";
    background-color: white;
    position: absolute;
    width: 100%;
    height: 2px;
  }
  .header .burger-btn::before {
    top: 0;
  }
  .header .burger-btn::after {
    bottom: 0;
  }
  .header .burger-btn span {
    position: absolute;
    background-color: white;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: transform 0.3s ease;
  }
  .header .burger-btn_active {
    position: fixed;
  }
  .header .burger-btn_active span {
    transform: rotate(45deg);
  }
  .header .burger-btn_active::before {
    transform: translateY(9px) rotate(-45deg);
  }
  .header .burger-btn_active::after {
    transform: translateY(-9px) rotate(-45deg);
  }
}

.best-place {
  margin-top: 153px;
}
.best-place__container {
  margin: 0 165px;
}
.best-place__wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.best-place__circle {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #B85C38;
  border: 2px solid white;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  background: none;
  transition: all ease 0.5s;
}
.best-place__circle::before {
  transition: all 0.5s ease;
  content: "";
  width: 0%;
  height: 100%;
  background: #B85C38;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.best-place__circle:hover::before {
  width: 100%;
}
.best-place__circle:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.best-place__img {
  fill: white;
}
.best-place__title {
  margin-top: 40px;
  font-size: 72px;
  color: white;
