  font-family: "Nunito";
  text-align: center;
  text-shadow: 4px 2px 4px 0px rgba(0, 0, 0, 0.25);
}
.best-place__description {
  text-align: center;
  margin-top: 16px;
  color: white;
  font-size: 24px;
  font-family: "Nunito";
}
.best-place__buttons {
  display: flex;
  margin-top: 40px;
}
.best-place__demo {
  padding: 18px 32px;
  color: white;
  border-radius: 40px;
  background-color: #B85C38;
  font-size: 20px;
  font-family: "Nunito";
  transition: all ease 0.5s;
}
.best-place__demo:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.best-place__buy {
  margin-left: 24px;
  color: white;
  padding: 18px 32px;
  border-radius: 40px;
  border: 2px solid #FFF;
  font-size: 20px;
  font-family: "Nunito";
  background-color: rgba(0, 0, 0, 0);
  margin-left: 24px;
  background: none;
  position: relative;
  overflow: hidden;
  transition: all ease 0.5s;
}
.best-place__buy::before {
  transition: all 0.5s ease;
  content: "";
  width: 0%;
  height: 100%;
  background: #B85C38;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.best-place__buy:hover::before {
  width: 100%;
}
.best-place__buy:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.best-place__buy-text {
  color: white;
}
